import { BaseElement, createClassName, defineElement, html, ifDefined, property } from '@umetrics/sartorius-ui-base';
import { menuStyles } from './menuStyle';
import { MenuProperties, MenuSlot } from './menuProperties';
import { version } from './metadata';
import { MenuItem } from './menuItem';

@defineElement({ tagName: 'menu', styles: menuStyles, version })
export class Menu extends BaseElement implements MenuProperties {
    // Static members

    // Public members
    @property({ type: String, reflect: true, attribute: 'menu-top-padding' })
    public menuTopPadding;
    @property({ type: String, reflect: true, attribute: 'menu-width' })
    public menuWidth;

    @property({ type: Boolean, reflect: true })
    get open() {
        return this._open;
    }
    set open(val) {
        if (!this._animating) {
            if (this._open && !val) {
                this._hide();
            } else if (!this._open && val) {
                this._show();
            }
        } // else, please slow down mode...
    }

    // Protected members

    // Private members
    private _open = false;
    private _animating = false;

    constructor() {
        super();

        this._keyDownListener = this._keyDownListener.bind(this);
    }

    // Protected methods
    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            open: this._open,
        });

        const style = this.menuTopPadding ? `top: ${this.menuTopPadding};` : '';
        let menuStyle = style + (this.menuWidth ? `width: ${this.menuWidth}px;` : '');
        menuStyle += this.menuWidth && !this._open ? `left: -${this.menuWidth}px;` : '';

        return html` <div class="${rootClasses}">
            <slot name="${MenuSlot.BUTTON_SLOT}" @click="${this._toggleOpen}"></slot>
            <div class="${baseClassName}-dropdown" style="${ifDefined(menuStyle)}" @click="${this._handleNavigated}">
                <slot name="${MenuSlot.LINK_SLOT}"></slot>
                <span class="${baseClassName}-bottom"><slot name="${MenuSlot.BOTTOM_SLOT}"></slot></span>
            </div>
            <div class="${baseClassName}-overlay" style="${ifDefined(style)}" @click="${this._toggleOpen}"></div>
        </div>`;
    }

    private _toggleOpen() {
        this.open = !this._open;
    }

    private _show() {
        this._open = true;
        document.addEventListener('keydown', this._keyDownListener);

        this.requestUpdate('open', 'false');
        this.updateComplete.then(() => {
            requestAnimationFrame(() => {
                const elem = this.shadowRoot!.querySelector('[class$="-dropdown"]') as HTMLDivElement;

                if (elem) {
                    elem.style.left = '0px';
                    this._addSingletonTransitionEventHandler(elem, () => {});

                    const overlay = elem.nextElementSibling as HTMLDivElement;
                    overlay.style.backgroundColor = 'rgba(0,0,0,0.5)';
                }
            });
        });
    }

    private _hide() {
        document.removeEventListener('keydown', this._keyDownListener);

        const element = this.shadowRoot!.querySelector('[class$="-dropdown"]') as HTMLDivElement;
        const requestUpdateFunc = () => {
            this._open = false;
            this.requestUpdate('open', 'true');
        };
        // Give the element a height to change from
        if (element) {
            const overlay = element.nextElementSibling as HTMLDivElement;
            overlay.style.removeProperty('background-color');

            if (this.menuWidth) {
                element.style.left = `-${this.menuWidth}px`;
            } else {
                element.style.removeProperty('left');
            }
            this._addSingletonTransitionEventHandler(element, requestUpdateFunc);
        } else {
            requestUpdateFunc();
        }
    }

    private _addSingletonTransitionEventHandler(elem: HTMLDivElement, callback) {
        this._animating = true;
        const timer = setTimeout(() => {
            if (this._animating) {
                callback();
                this._animating = false;
            }
        }, 500);

        elem.addEventListener(
            'transitionend',
            () => {
                clearTimeout(timer);
                callback();
                this._animating = false;
            },
            { once: true }
        );
    }

    private _handleNavigated(e) {
        if (e.target instanceof MenuItem) {
            if (!e.target.active && !e.target.disabled) {
                this._toggleOpen();
            }
        }
    }

    private _keyDownListener(e) {
        if (e.key === 'Escape') {
            this._toggleOpen();
            e.preventDefault();
            return;
        } else if (e.key !== 'Tab') {
            return;
        }

        this.updateComplete.then(() => {
            const slot = this.shadowRoot!.querySelector(`slot[name=${MenuSlot.LINK_SLOT}]`) as HTMLSlotElement;
            const nodes = slot.assignedNodes().filter((node) => node.nodeType === 1 && !(node as MenuItem).active);

            const buttonSlot = this.shadowRoot!.querySelector(`slot[name=${MenuSlot.BUTTON_SLOT}]`) as HTMLSlotElement;
            const buttonNode = buttonSlot.assignedNodes().filter((node) => node.nodeType === 1)[0] as HTMLButtonElement;

            if (nodes.length > 0) {
                const first = nodes[0] as MenuItem;
                const last = nodes[nodes.length - 1] as MenuItem;

                if ((e.target === last && !e.shiftKey) || (e.target === first && e.shiftKey)) {
                    buttonNode.focus();
                    e.preventDefault();
                } else if (e.target === buttonNode && e.shiftKey) {
                    last.focus();
                    e.preventDefault();
                }
            } else {
                buttonNode.focus();
                e.preventDefault();
            }
        });
    }
}
