import {
    cssSelector,
    cssVariableAsVar,
    doMediaquery,
    GenerateStyles,
    style,
    unsafeCSS,
} from '@umetrics/sartorius-ui-base';

export const menuStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        unsafeCSS(`
       
           :host {
                display: inline-block;
                height: 100%;
                width: 100%;
            }
            
           ${root} {
                width: 100%;
                height: 100%;
            }
                                              
            ${child('dropdown')} {
                display: none;
                overflow: hidden;
                transition: left ${cssVariableAsVar('expand_transition_duration')} ease-in-out 0s;
                background-color: ${cssVariableAsVar('color_primary_3')};
                white-space: nowrap;
                height: auto;
                position: fixed;
                top: 0px;
                bottom: 0px;
                left: -200px;
                width: 200px;
                z-index: 100;
            }
            
            ${child('bottom')} {
                position: absolute;
                bottom: 0;
                color: ${cssVariableAsVar('color_primary_2')};
                font-size: 1rem;
            }
            
            ${child('overlay')} {
                display: none;
                position: absolute;
                top: 48px;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(0,0,0,0.0);
                transition: background-color ${cssVariableAsVar('expand_transition_duration')} ease-in-out 0s;
                z-index: 99;
                cursor: pointer;    
            }
            
            ${is('open')} ${child('dropdown')},             
            ${is('open')} ${child('overlay')} {
                display: block;
            }
            
            ${doMediaquery(
                'mobile',
                `
                ${child('dropdown')} {
                    width: 100%;
                    transition: left 0s ease-in-out 0s;
                }
            `
            )}
        `),
    ];
};
