import { cssSelector, cssVariableAsVar, GenerateStyles, style, unsafeCSS } from '@umetrics/sartorius-ui-base';

export const menuItemStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        unsafeCSS(`
                   
            ${root} {
                display: block;
                flex: 1;
                color: ${cssVariableAsVar('color_primary_2')};
                padding: 12px 16px;
                text-decoration: none;
                position: relative;
                outline: 0px;
                font-size: 1rem;
                transition: background-color ${cssVariableAsVar(
                    'hover_transition_duration'
                )} ease-in-out 0s, color ${cssVariableAsVar('hover_transition_duration')} ease-in-out 0s;
            }
            
            ${child('inner')} {
                display: flex;
                align-items: center;
            }
            
            ::slotted([slot=icon-slot]) {
                margin-right: 12px;
            }
            
            ${is('!active')}:hover {
                background-color: ${cssVariableAsVar('color_primary_1')};
                color: ${cssVariableAsVar('color_primary_3')};
            }
            ${is('!active')}:focus-visible:before {
                content: " ";
                display: block;
 
                position: absolute;
                inset: 2px 2px 2px 2px;
                border: 2px solid ${cssVariableAsVar('color_primary_2')};
            }
            ${is('active')} {
                background-color: ${cssVariableAsVar('color_secondary_10')};
                pointer-events: none;
            }
            
        `),
    ];
};
