import { BaseElement, createClassName, defineElement, html, property, query } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { menuItemStyles } from './menuItemStyle';
import { MenuItemProperties, MenuItemSlot } from './menuItemProperties';

@defineElement({ tagName: 'menu-item', styles: menuItemStyles, version })
export class MenuItem extends BaseElement implements MenuItemProperties {
    // Static members

    // Private members
    private _active = false;
    private _href = '';
    @query('a')
    private _a;

    // Protected members

    // Public members
    @property({ type: String, reflect: true, attribute: 'href' })
    public get href() {
        return this._href;
    }
    public set href(val) {
        const prev = this._href;
        if (prev !== val) {
            this._href = val;
            this.requestUpdate('href', prev);
        }
    }
    @property({ type: Boolean, reflect: true })
    public get active() {
        return this._active;
    }
    public set active(val) {
        const prev = this._active;
        if (prev !== val) {
            this._active = val;
            this.requestUpdate('active', '' + prev);
        }
    }

    public focus() {
        this._a.focus();
    }
    public blur() {
        this._a.blur();
    }

    // Protected methods

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            active: this.active,
        });

        return html`<a class="${baseClassName} ${rootClasses}" href="${this.href}" tabindex="${this.active ? -1 : 0}"
            ><div class="${baseClassName}-inner"><slot name="${MenuItemSlot.ICON_SLOT}"></slot><slot></slot></div
        ></a>`;
    }
}
